import logo from './logo.svg';
import './App.css';
import React, {useEffect, useState} from 'react'

function App() {
  const [message, setMessage] = useState([]);
  useEffect(() => {
    fetch("/api/Ver_Demo")
        .then((response) => {
          return response.json();
        })
        .then((data) => {
          setMessage(data);
        });
  }, []);

  return (
    <div>
      {message}
    </div>
  );
}

export default App;
